		methods: {
			getData() {
				uni.showLoading({
					title: "数据加载中",
					mask: true
				})
				uni.request({
					url: 'http://120.46.160.16:8088/questionnaires/display',
					method: 'POST',
					data: {
						questionnaire_id: 3
					},
					success: res => {
						console.log(res);
						if (res.data.data.published) {
							uni.showToast({
								title: '未发布'
							})
						} else {
							this.title = res.data.data.title;
							this.introduce = res.data.data.description;
							this.questionnaireId = res.data.data.questionnaire_id;
							uni.request({
								url: 'http://120.46.160.16:8088/questions/display',
								method: 'POST',
								data: {
