									console.log(res.data);
									for (let i = 0; i < this.dataArr.length; i++) {
										if (this.dataArr[i].type === 'single') {
											this.answerSheet.push({
												type: 'single',
												radio: -1
											})
										} else if (this.dataArr[i].type === 'multi') {
